@font-face {font-family: 'iconfont';
    src: url('@{fa-font-path}/iconfont.eot'); /* IE9*/
    src: url('@{fa-font-path}/iconfont.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('@{fa-font-path}/iconfont.woff') format('woff'), /* chrome、firefox */
    url('@{fa-font-path}/iconfont.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
    url('@{fa-font-path}/iconfont.svg#iconfont') format('svg'); /* iOS 4.1- */
}
.iconfont {
  font-family:"iconfont" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
.icon-shouqi:before { content: "\e63e"; }
.icon-zhankai:before { content: "\e62c"; }

