//网页下方社交媒体样式
ul.social{
    margin:0;
    padding:0;
    width:100%;
    text-align:center;
    >li{
        display:inline-block;
        >a{
            display:inline-block;
            font-size:18px;
            line-height:30px;
            .square(30px);
            border-radius:36px;
            background-color:@brand-primary;
            color:#fff;
            margin:0 3px 3px 0;
                
                &:hover{
                    text-decoration:none;
                    background-color:@link-hover-color;
                }
        }
        
    }
}


/*
 * ZeroClipboard styles
 */
.zero-clipboard {
  position: relative;
  display: none;
  
  @media (min-width: 768px) {
    display: block;
  }
  
  .clip_button {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
    display: block;
    padding: 5px 8px;
    font-size: 12px;
    color: #767676;
    cursor: pointer;
    background-color: #fff;
    border: 1px solid #e1e1e8;
    border-radius: 0 4px 0 4px;
    
    @media (min-width: 768px) {
      border-top-right-radius: 0;
    }
    
    &.zeroclipboard-is-hover { 
      color: #fff;
      background-color: #563d7c;
      border-color: #563d7c;
    }
 
    &.zeroclipboard-is-active {
      color: #fff;
      background-color: #563d7c;
      border-color: #563d7c;
    }
  }
}


