.doc-side-container{
    //小屏幕
    @media(max-width:(@grid-float-breakpoint - 1)){
        background-color: @body-bg;
        border: 1px solid #563d7c;  
        position:fixed;
        top:80px;
        right:45px;
        z-index:10;
    }
    //大屏幕
    @media(min-width:@grid-float-breakpoint){
        &.collapse {
          display: block !important;
        }
        background-color: transparent;
        border: 0;    
    }
    
}
.doc-sidebar {
    .nav {
        /* All levels of nav
        超链接元素
        */  
        > li > a {
          display: block;
          padding: 4px 20px;
          font-size: 13px;
          font-weight: 500;
          color: #767676;
      
          &:hover,
          &:focus {
            padding-left: 19px;
            color: #563d7c;
            text-decoration: none;
            background-color: transparent;
          }
        }
        /*活动元素*/
        .active {
            > a,
            &:hover>a,
            &:focus>a{
                padding-left: 18px;
                font-weight: bold;
                color: #563d7c;
                background-color: transparent;
            }
        }

        //second level
        .nav {
          display: none; /* Hide by default, but at >768px, show it */
          padding-bottom: 5px;
          
          > li >a{
                padding-top: 1px;
                padding-bottom: 1px;
                padding-left: 30px;
                font-size: 12px;
                font-weight: normal;    
                &:hover,
                &:focus {
                  padding-left: 29px;    
                }               
          }
          //二级菜单的活动元素
          >.active,> .active:hover,> .active:focuse{
            >a{
                padding-left: 28px;
                font-weight: 500;
            }  
          }      
        }
        // 足够宽时，显示活动元素的二级菜单
        @media (min-width: @screen-md-min) {
        >.active > .nav{
            display: block;
            }
        }
    }
    
    /*一级菜单的活动元素加边线*/
    > .nav{
        > .active{
            >a,
            &:hover>a,
            &:focus>a{
                border-left: 1px solid #563d7c;    
            }
        }       
    }
    
    
}

